
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { OfficialsealList, uploadAttachment, startApplication } from '@/api/index'
import { mapGetters } from 'vuex'
export default {
name: 'applyLeave',
components: {
PopupPicker, Group, XInput, PopupRadio, Datetime, XTextarea, Loading, XButton
},
data () {
return {
Officialseal: [],
loading: false,
form: {
type: 11,
companyId: '',
loanItemId: '',
reason: '',
attachmentList: []


},
ProfileList: []    
}
},
methods: {
uploadAttachment (e) {
if (e.target.files.length > 0) {
this.loading = true
}
const fd = new FormData()
const file = e.target.files[0]


fd.append('file', file)
uploadAttachment(fd)
.then(res => {

this.ProfileList.push({attachmentName: file.name, attachment: res.result})
this.loading = false
this.$vux.toast.show({
text: res.message,
type: 'success'
})
})
.catch(() => {
this.$vux.toast.show({
text: '上传失败',
type: 'error'
})
this.loading = false
})
console.log(this.ProfileList)
},
showseal () {
this.Officialseal = []
this.getOfficialsealList()
},
getOfficialsealList () {
var allsealList = []
const data = {
companyId: this.form.companyId,
type: 1
}
OfficialsealList(data).then(res => {
allsealList = res.result
allsealList.forEach(e => {
this.Officialseal.push({key: e.id, value: e.name})
})
})
},
startLeave () {
this.form.attachmentList = this.ProfileList
startApplication(this.form)
.then(res => {
this.$vux.toast.show({
text: res.message,
type: 'success'
})
this.$router.go(-1)
})
},

deleteImg (index) {
this.ProfileList.splice(index, 1)
console.log(this.ProfileList)
}
},
computed: {
...mapGetters(['applyLeaveList'])
}
}



